.carousel-container{position:relative}.carousel-container .carousel-item-list-wrapper{position:relative;overflow:hidden}.carousel-container .carousel-item-list{position:relative;white-space:nowrap;top:0;left:0;-webkit-transform:translateZ(0);transform:translateZ(0)}.carousel-container .carousel-item-list .carousel-item{display:inline-block;width:100%;vertical-align:top}.carousel-container .pagination{position:absolute;bottom:12px;width:100%;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center}.carousel-container .pagination .bullet{width:7px;height:7px;margin:4px;display:inline-block;border-radius:100%;background-color:#d3dee3}.carousel-container .pagination .active{background-color:#2a8fbd}



